.comments {
    margin-top: 50px; }

.Video {
    margin: 30px 0 50px 0; }

.Video__heading {
    margin: 0 0 10px 0;
    color: white;
 }    //yellow

.Video__description {
    font-size: 18px;
    font-weight: normal; }

.Video__button {
    float: right;
    margin: 10px 0 30px;
    background-color: #4CAF50;
    border: none;
    color: white;

    &:hover,
    &:active,
    &:focus {
        background-color: #1E8C22;
        color: white; }

    &--lecture {
        background-color: #8400D6;

        &:hover,
        &:active,
        &:focus {
            background-color: #5B0691;
            color: white; } } }


#ohsnap {
    position: fixed;
    bottom: 5px;
    right: 5px;
    margin: 0;
    z-index: 10000000001; }

.alert {
    text-align: right;
    padding: 15px;
    margin: 0 0 5px 0;
    border-radius: 0;
    float: right;
    clear: right;
    color: white;
    font-size: 14px; }

.alert-red {
    background-color: red; }

.alert-green {
    background-color: green; }

video {
    padding: 0;
    margin: 0 auto;
    display: block; }

.video-section {
    padding: 0;
    margin: 0;
    background: yellow; }

.video-js {
    margin: 30px auto 0; }

.video-heading {
    padding: 80px 0 0;
    margin: 0;
    margin-top: 50px;
    color: #FF5757 !important;
    font-weight: bold; }

.main-heading {
    color: #FF5757 !important;
    font-weight: bold; }

.lectures-button {}

.comments__heading {
    color: #0B4975; }

body {
    font-family: 'Roboto Slab', serif; }

.modal-actions i {
    color: green;
    visibility: hidden; }

h2 {
    text-align: center;
    font-weight: normal; }

.main-heading,
.email-heading {
    color: #575757; }

button {
    color: white;
    &:hover,
    &:active,
    &:focus {
        color: white; } }

.error {
    color: red;
    display: block;
    font-size: 16px;
    text-align: right; }

h3 {
    text-align: left;
    font-size: 30px;
    font-weight: normal; }

.centered {
    text-align: center; }

.last-link {
    padding-right: 0; }

.space {
    width: 300px;
    height: 300px;
    border-radius: 50%; }

.description {
    font-size: 20px;
    margin-bottom: 50px; }

.program-list {
    list-style: square;
    font-size: 20px;
    text-align: left;
    margin-top: 25px; }

.mail-img {
    margin: 0 auto 50px;
    display: block;
    width: 300px; }

.link-button {
    border-radius: 4px;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 26px;
    padding-top: 10px;
    cursor: pointer;
    width: 300px;
    height: 50px;
    display: inline-block; }

h1 {
    font-size: 36px;
    font-weight: 100;
    margin: 0;

    span {
        color: #0B4975;
        font-weight: bold; } }

.section {
    padding: 50px 30px; }

.section-orange {
    background-color: #FF5200;
    color: white;

    .Video__heading {
        color: white; } }

.section-blue {
    background-color: #0597ff;
    color: white; }

.section-red {
    background: #F14040;
    font-size: 26px;
    color: white; }

.section-purple {
    background: #6C00AF;
    color: white;
    font-weight: 300; }

.price {
    color: #42F742;
    font-weight: bold; }

.section-dark-blue {
    background-color: #0C2232; }


.payment-button {
    background-color: #56D056;
    margin-right: 20px;

    &:hover {
        background-color: #36BE36;
        color: white; } }

.register-button {
    background-color: #56D056;
    margin-right: 20px;

    &:hover {
        background-color: #36BE36;
        color: white; } }

.login-button {
    margin: 0;
    background-color: #FFCB0D;

    &:hover {
        background-color: #EBBA06;
        color: white; } }

.email-button {
    margin: 0;
    background-color: #03A9F4;

    &:hover {
        background-color: #0389C6;
        color: white; } }

.main-button {
    margin: 50px 0 0 0;
    background-color: #FF5757;

    &:hover {
        background-color: #FF3333;
        color: white; } }

.account-buttons {
    margin-top: 50px; }

nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #0B4975;
    height: 50px;
    z-index: 1000;
    overflow: hidden;
    transition: all 0.3s ease-in-out;

    a {
        color: white;

        &:hover {
            color: #78D5FF; } } }

.hamburger-button {
    display: none;
    padding: 0;
    margin: 3px 0;
    border: none;
    color: white;
    font-size: 28px;

    &:hover {
        color: #33C3F0; }

    &:active,
    &:focus {
        color: white; } }


.nav-inner {
    height: 50px;
    padding-top: 3px; }

.nav-list {
    margin: 0;
    padding: 0;
    height: 50px; }

.nav-list-item {
    display: inline-block;
    padding: 0;
    margin: 0;

    .active {
        color: #78D5FF; } }

a {
    text-decoration: none; }

.nav-list-link {
    padding: 6px 10px;
    font-weight: normal;
    font-size: 20px;
    display: inline-block; }

.logo {
    font-size: 28px;
    font-weight: bold;
    display: inline-block; }

.logo-icon {
    margin-right: 10px;
    color: #FFCB0D; }

.social-list {
    list-style: none;
    margin: 0;
    padding: 20px 0; }

.social-list-item {
    width: 50px;
    height: 50px;
    display: inline-block;
    margin: 0 10px; }

input,
textarea,
select,
fieldset {
    margin-bottom: 10px; }

.modal-actions {
    text-align: right;
    margin-top: 10px; }

.remodal-cancel {
    margin-right: 10px; }

.remodal {
    max-width: 500px;
    padding-bottom: 0; }

.remodal-cancel,
.remodal-confirm {
    height: 40px;
    color: white;
    padding: 10px 10px 35px;

    &:hover {
        color: white; } }

.modal-form {
    text-align: left; }

.social-list-link {
    width: 100%;
    height: 100%;
    display: inline-block;
    font-size: 24px;
    border-radius: 50%;
    border: 2px solid silver;
    color: silver;
    line-height: 2;

    &:hover {
        border-color: white;
        color: #0C2232;
        background: white; }

    i {
        padding-top: 12px; } }

.copyright {
    padding: 10px 0 20px;
    font-weight: 100;
    color: silver; }

@media screen and (min-width: 850px) {
    main {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 100vh;
        flex-wrap: wrap;
        text-align: center; }

    .nav-list {
        float: right; } }

@media screen and (max-width: 850px) {
    nav.open {
        height: 90px; }

    .nav-list {
        float: none;
        text-align: center; }

    &.open {
        height: auto; }

    .hamburger-button {
        display: inline-block; }

    .register-button {
        margin: 0 0 20px 0; }

    .nav-list-link {
        font-size: 16px; }

    main {
        padding: 70px 0;
        display: block;
        flex-wrap: wrap;
        text-align: center; } }

